import React ,{Component} from 'react';
import './App.css';

class App extends Component {
 constructor(props){
  super(props);
  this.state={
    items:[],
    isLoaded:false,
  }
}
  // methods
componentDidMount(){

   fetch('http://newsapi.org/v2/everything?q=bitcoin&from=2020-05-29&sortBy=publishedAt&apiKey=9ae070cfd2c64186a55471eaaaedfc6d')
   .then(res=>res.json())
   .then(json=>{
     this.setState({
      isLoaded:true,
      items:json,
     })
   });

}

render(){
//displaying data

  var{ isLoaded,items }=this.state;
  if(!isLoaded){
    return<div className="preload">Loading...</div>;
  }
  else{
  return(
<div className="App">
<nav className="navbar navbar-expand-sm fixed-top justify-content-center"v style={{textAlign:"center",color:"gold"}}>
<h1 className="nav-head" style={{textAlign:"center",}}>Crypto <i class="fa fa-btc" aria-hidden="true"></i> News</h1>
</nav>
  {items.articles.map(item=>(
<div key={item.title}>
<div className="container" style={{paddingTop:40}}>
  <div className="row">
  <div className="col" style={{padding:20}}>
  <div class="card">
  <div class="card-header"><h2>{item.title}</h2></div>
  <div class="card-body"><h6>{item.description}</h6>
  <div className="container">
    <img className="img-fluid" src={item.urlToImage} style={{padding:10}} alt=""/>
    <h6>{item.content}</h6>
    </div>
    </div>
  <div className="card-footer"><h4>Author: {item.author}</h4>
  <h5><a href={item.url}>GOTO link</a></h5>
  <h5>Published at: {item.publishedAt}</h5>
  </div>
  
</div>
    </div>
    </div>
  </div>
</div>
 ))}

</div>
    );
}
}
}
export default App;
